body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: rgb(241, 241, 241);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display:flex;
  align-items: center;
}

.Logo {
  box-sizing: border-box;
  width:90vw;
  max-width:500px;
  padding:2em;
  margin-left:auto;
  margin-right:auto;
  animation: appear 0.4s;
}


.Controls {
  height:100vh;
  max-width:400px;
  padding:2em;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

ul {
  padding:0;
}

.desc {
  display: block;
  margin-bottom:1em;
}

h1 {
  max-width: 80%;
  font-size:1.7em;
}

h2 {
  font-size:1.2em;
  margin-bottom:0.2em;
}

.Button {
  display: inline-block;
  box-sizing: border-box;
  cursor:pointer;
  padding:0.5em;
  margin-bottom: 1em;
  width: auto;
  background: #0c31d3;
  box-shadow: 0 2px 2px rgb(119, 119, 119);
  border-radius: 2px;
  transition: all 0.2s;
  color:white;
}

.Button:hover{
  background: #2047f3;
}

.Button:active {
  transform: scale(0.96);
  box-shadow: 0 2px 2px rgb(68, 68, 68);
}

.Button span {
  user-select: none;
}

@keyframes appear {
  0% {
    opacity:0;
    transform: scale(0.90);
  }

  40% {
    opacity:0.8;
    transform: scale(1.02);
  }
  
  100% {
    opacity: 1;
    transform: scale(1)
  }
}

@media screen and (max-width: 700px) {
  .App {
    flex-direction: column;
  }
}